@import "fonts.less";
@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400');
@import "../css/font-awesome.min.css";
@import url("https://cdnjs.cloudflare.com/ajax/libs/simple-line-icons/2.4.1/css/simple-line-icons.css");
@import "mixins.less";
@import "chosen.less";

@navbar-title: #fff;
@placeholder-color: #fff;
@brand-primary: rgba(81, 81, 81, 0.8);;
@border-radius: 0;


.margin-top {
    margin-top: 15px;
}

h1, h2, h3, h4, h5 {
    color: #FFFFFF !important;
}

* {
    outline: none !important;
    font-family: 'Roboto Condensed';
    text-transform: uppercase;
    color: #FFFFFF;
    border-radius: 0 !important;
    -webkit-transition: background-color .3s;
    -moz-transition: background-color .3s;
    -ms-transition: background-color .3s;
    -o-transition: background-color .3s;
    transition: background-color .3s;

    &::-webkit-input-placeholder {
        color: @placeholder-color;
    }
    &:-moz-placeholder {
        /* FF 4-18 */
        color: @placeholder-color;
    }
    &::-moz-placeholder {
        /* FF 19+ */
        color: @placeholder-color;
    }
    &:-ms-input-placeholder {
        /* IE 10+ */
        color: @placeholder-color
    }

}

.form-control{
    border: none !important;
    box-shadow: none !important;
}

a {
    text-decoration: none;

    &:hover {
        text-decoration: none !important;
    }
}

.invisible {
    opacity: 0;
}


ul {
    padding-left: 0;
    list-style: none;
}

#external-backlink {
    position: absolute;
    left: 10px;
    bottom: 5px;

    > a{
        color: #ffffff;
    }
}

.custom-scroll{
    overflow-y: hidden;
}


.btn {
    border-radius: @border-radius !important;

    &.btn-primary{
        background-color: @brand-primary;
        border-color: #FFFFFF;
        padding: 7.2px 18px;
        line-height: 1.5;
        .transition(background-color .3s);

        &:active {
            &:hover, &.active, &:focus, &:active {
                background-color: darken(@brand-primary,15%);
                border-color: #FFFFFF;
            }
        }
        &:hover, &.active, &:focus, &:active {
            background-color: darken(@brand-primary,10%);
            border-color: #FFFFFF;
        }
    }
}
.swal-modal {
    background-color: rgba(0, 0, 0, 0.91);

    .swal-title {
        color: #ffffff;
    }
    .swal-text {
        color: rgba(255, 255, 255, 0.91);
        text-align: center;
    }
    .swal-button {
        background-color: @brand-primary;

        &:active {
            &:hover, &.active, &:focus, &:active {
                background-color: darken(@brand-primary,15%);
                border-color: #FFFFFF;
            }
        }
        &:hover, &.active, &:focus, &:active {
            background-color: darken(@brand-primary,10%);
            border-color: #FFFFFF;
        }
    }
    .swal-icon {
        border-radius: 50% !important;

        &:before, &:after{
            background-color: transparent !important;
        }
        .swal-icon--success__hide-corners {
            background-color: transparent !important;
        }
        .swal-icon--success__ring {
            border-radius: 50% !important;
        }
    }
}

.gold-text {
    color: #FFD700;
}

body {
    background: url("../images/backgrounds/new-bg.jpg");
    background-position-x: center;
    background-size: cover;
    background-attachment: fixed;
    height: 100%;
    color: #fff;
    font-family: 'Roboto Condensed';
    font-size: 18px;
    text-transform: uppercase;
    overflow-x: hidden;

    header {
        #userinfo-nav {
            height: 22px;
            font-size: 12.5px;
            line-height: 1.8;
            background: rgba(255, 255, 255, 0.05);

            ul {
                list-style: none;

                li {
                    display: inline;
                    color: #ffffff;

                    a {
                        color: #ffffff;
                    }
                }

                &.pull-left {
                    margin-left: 10px;
                    li {
                        margin-right: 24px;
                    }
                }

                &.pull-right {
                    li {
                        margin-right: 41px;
                    }
                }
            }
        }

        #main-nav {
            height: 127px;
            clear: both;
            margin-top: 29px;

            ul {
                padding-left: 0;
                list-style: none;

                li {
                    width: 190px;
                    text-align: center;
                    margin-bottom: 1px;

                    a {
                        display: block;
                        opacity: 0.45;
                        width: 149px;
                        height: 38px;
                        line-height: 1.5;
                        margin: 0 auto;
                        font-size: 17px;
                        color: #fff;
                        font-weight: 800;
                        padding: 5.5px;

                        &:hover {
                            text-decoration: none;
                        }
                    }

                    &.header_button {
                        background: url('../images/buttons/header_sprites_buttons.png') no-repeat;
                        width: 190px;
                        height: 37px;

                        &:hover {
                            background-position: -1px -40px !important;
                        }

                        &.header_normal_button {
                            background-position: -1px -79px;
                        }

                        &.header_disabled_button {
                            background-position: -1px -1px;
                        }
                    }
                }
            }
        }

        .header-buttons {
            width: 367px;
            height: auto;
            margin-bottom: 0;
        }

        .main-logo {
            padding-bottom: 41px;
            padding-top: 41px;
            margin-left: 90px;

            &:hover {
                > img {
                    filter: drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.7));
                    -webkit-filter: drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.7));
                }
            }

            > img {
                width: 210px;
                height: 101px;
                -webkit-transition: filter .3s;
                -moz-transition: filter .3s;
                -ms-transition: filter .3s;
                -o-transition: filter .3s;
                transition: filter .3s;
            }
        }

        #left-buttons {
            float: left;

            .main-right-buttons {
                position: relative;
                top: 29px;
                right: 6px;
            }

            .main-left-buttons {
                position: relative;
                top: -28px;
            }
        }

        #right-buttons {
            float: right;

            .main-left-buttons {
                position: relative;
                top: 29px;
                left: 6px;
            }

            .main-right-buttons {
                position: relative;
                top: -28px;
            }
        }

        .main-left-buttons {
            float: left;
        }

        .main-right-buttons {
            float: right;
        }
    }

    footer {
        height: 50px;

        .footer-inner {
            background: rgba(145, 145, 145, 0.21);
            padding: 10px 20px;

            #footer-logo {
                margin: 0 auto;
                width: 132px;
                height: 34px;
            }

            #footer-backlink {
                margin-top: -25px;
                float: right;
                text-decoration: none;
                font-weight: 300;

                a {
                    color: #FFFFFF;
                }
            }

            span {
                float: left;
                margin-top: 10px;
                color: #FFFFFF;
                font-size: 15px;
                font-weight: 300;
            }
        }
    }

    &.xmas:not(.external) {

        #tv-box {
            background: url("../images/internalHome/tv/xmas-tv-overlay.png") !important;
            height: 301px !important;
            z-index: 9999999;
            position: relative;
        }

        header {
            #main-nav {
                #left-buttons {
                    &:before {
                        content: "";
