                        height: 247px;
                        position: absolute;
                        background-image: url(../images/header/xmas_btn_left.png);
                        background-repeat: no-repeat;
                        background-position-y: -45px;
                        margin-left: -30px;

                    }
                }
                #right-buttons {
                    &:before {
                        content: "";
                        width: 450px;
                        height: 247px;
                        position: absolute;
