                    background-image: url("../images/internalCompanyChoose/bg_character_eic.jpg");
                }
                &.vru {
                    background-image: url("../images/internalCompanyChoose/bg_character_vru.jpg");
                }

                &:hover {
                    -moz-box-shadow: inset 0 0 10px #FFFFFF;
                    -webkit-box-shadow: inset 0 0 10px #FFFFFF;
                    box-shadow: inset 0 0 10px #FFFFFF;
                    border: 1px white solid;
                    opacity: 1;
                    filter: brightness(100%);
                    cursor: pointer;
                }
            }
        }

        .story {
            display: none;
            opacity: 0;
            font-size: 27px;
            color: #FFFFFF;

