    &.internalStart {
        #playerInfo-tv-container {
            display: inline-block;
            width: 100%;
        }

        /* Player info profile */

        #player-info {
            width: 272px;
            margin-left: 43px;
            float: left;
        }

        #player-info-profile {
            width: 260px;
            height: 120px;
            margin: 0 auto;
            border: 1px solid rgba(255, 255, 255, 0.1);
            .transition(opacity .6s);
        }

        #player-info-profile-header {
            text-align: right;
            font-size: 16px;
        }

        #player-info-profile-header span {
            margin-right: 21px;
        }

        #player-info-profile-body {
            background: rgba(255, 255, 255, 0.1);
            width: 254px;
            height: 94px;
            margin: 0 auto;
        }

        #player-info-profile-body ul {
            width: 160px;
            text-align: center;
            font-size: 15.3px;
            margin-top: 15px;
        }

        #player-info-profile-body ul p {
            margin: 0;
        }

        /* @end player info profile */

        #player-info-name {
            font-size: 41.67px;
            text-align: center;
            /* In the psd are 20px but looks excesive */
            margin-top: 7px;
            .transition(opacity .6s);

            > p {
                opacity: 0.65;
                margin-bottom: 0;
                -webkit-text-fill-color: rgba(255, 255, 255, 0);
                -webkit-text-stroke-width: .5px;
                -webkit-text-stroke-color: rgba(255, 255, 255, .65);

                -webkit-animation: player-name-fade 3s linear 0s infinite normal;
                animation: player-name-fade 3s linear 0s infinite normal;
            }

            @-webkit-keyframes player-name-fade {
                0% {
                    opacity: .65;
                }
                50% {
                    opacity: 0;
                }
                100% {
                    opacity: .65;
                }
            }

            @keyframes player-name-fade {
                0% {
                    opacity: .65;
                }
                50% {
                    opacity: 0;
                }
                100% {
                    opacity: .65;
                }
            }
        }

        #player-info-equipment {
            width: 260px;
            height: 118px;
            margin: 0 auto;
            margin-bottom: 15px;
            clear: both;
            border: 1px solid rgba(255, 255, 255, 0.1);
            .transition(opacity .6s);
        }

        #player-info-equipment-body {
            background: rgba(255, 255, 255, 0.1);
            width: 254px;
            height: 112px;
            margin: 0 auto;
            margin-top: 2px;
            text-align: center;
            padding: 9px;
        }
        #player-info-equipment-body > p {
            margin-top: 2px;
            margin-bottom: 3px;
        }

        /* Player ship box */
        #player-ship-box {
            width: 360px;
            float: left;
            margin-top: 65px;
            margin-bottom: 25px;
            //height: 250px;

            #ship {
                margin: 0 auto;
                display: block;
                width: auto;
                height: auto;
                cursor: pointer;
                -webkit-transition: filter .6s;
                -moz-transition: filter .6s;
                -ms-transition: filter .6s;
                -o-transition: filter .6s;
                transition: filter .6s;
            }
            #pet {
                display: block;
                max-width: 76px;
                max-height: 86px;
                width: auto;
                height: auto;
                float: right;
                /* this value is for the goliath enforcer... each ship moves the fucking pet */
                margin-top: -13px;
                margin-right: 55px;
            }

            &:hover {
                #ship {
                    filter: drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.7));
                    -webkit-filter: drop-shadow(0px 0px 8px rgba(255, 255, 255, 0.7));
                }
            }
        }

        /* @end player ship box */

        /* TV box */

        #tv-box {
            background: url("../images/internalHome/tv/tv_border.png");
            width: 452px;
            height: 290px;
            float: right;
            margin-top: -22px;
        }

        #tv-state {
            width: 447.5px;
            height: 235px;
            position: relative;
            top: 53px;
            left: 2px;
            cursor: pointer;
        	#tv-off {
            	background: url("../images/internalHome/tv/tv_off.png") no-repeat center center;
            	width: 100%;
            	height: 100%;
            	z-index: 2;
            	position: absolute;
        	}
        	#tv-on {
				background: url("https://thumbs.gfycat.com/AstonishingGlitteringArizonaalligatorlizard-size_restricted.gif") no-repeat center center;
				width: 100%;
            	height: 100%;
            	z-index: 3;
            	position: absolute;
			}
        }
        /* @end TV box */

        #page-container {
            background: rgba(255, 255, 255, 0.02);
            clear: both;
            position: relative;
            top: 12px;
            padding: 13px 28px;
            padding-bottom: 0;
            width: 992px;
            margin: 0 auto;
        }

        #tabs-container {
            background: rgba(192, 192, 192, 0.1);
            padding: 13px;
        }

        .nav-tabs {
            border-bottom: none;
            padding-top: 5px;
        }

        .nav-tabs > li.active > a,
        .nav-tabs > li.active > a:focus,
        .nav-tabs > li > a:hover {
            color: #fff;
            cursor: pointer;
            background-color: transparent;
            border: none;
            opacity: 1 !important;
        }

        .nav-tabs > li > a {
            margin-right: 2px;
            line-height: 1.42857143;
            border: none;
            border-radius: 0;
            font-size: 17px;
            color: #FFFFFF;
            opacity: 0.5;
            .transition(opacity .3s)
        }

        .nav > li > a {
            position: relative;
            display: block;
            padding: 10px 3px;
            padding-top: 0px;
            opacity: 0.5;
        }

        /* TAB HOVER */

        .nav-tabs > li > a:hover {
            border-color: transparent;
        }

        .nav > li > a:focus, .nav > li > a:hover, .active {
            text-decoration: none;
            background-color: transparent;

        }

        /* CENTER TABS*/

        .nav-tabs > li, .nav-pills > li {
            float: none;
            display: inline-block;
            *display: inline;
            /* ie7 fix */
            zoom: 1;
            /* hasLayout ie7 trigger */
        }

        .nav-tabs, .nav-pills {
            text-align: center;
        }

        #tabs-content {
            background: rgba(0, 0, 0, 0.1);
            height: 200px;
        }

        /* STATS TAB */
        #stats table {
            margin: 0 auto;
            text-align: center;
            margin-top: 10px;
        }

        #stats table th, #stats table td {
            padding-right: 37.5px;
